import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { NavController, ToastController } from 'ionic-angular';
import { Observable } from 'rxjs/Rx';
import { Task, tasks } from '../../app/tasks';
import { Inspector, getRandomPositionInMoscow } from '../../app/inspectors';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
  tasks = [];
  inspectors = [];
  private readonly timeToCut = 600;

  constructor(private http: HttpClient, private toastCtrl: ToastController, public navCtrl: NavController) {

  }

  ionViewWillEnter() {
    this.getTasks();
    this.getInspectors();
  }

  getTasks(): Observable<Task[]> {
    this.http.get<Task[]>(`https://nwavetest.herokuapp.com/getalltasks`).subscribe(data => {
      this.tasks = data.tasks;
      tasks[0] = data.tasks.length;
    });
  }

  getInspectors(): Observable<Inspector[]> {
    this.http.get<Inspector[]>(`https://nwavetest.herokuapp.com/getallinspectors`).subscribe(data => {
      this.inspectors = data.inspectors;
    });
  }

  addInspector() {
    this.http.post(`https://nwavetest.herokuapp.com/newinspector`, {
      position: getRandomPositionInMoscow()
    }).subscribe(data => {
      this.getTasks();
      this.getInspectors();
    });
    // const id = this.inspectors.length + 1;
    // const inspectorsArray = inspectors.concat();
    // inspectorsArray.sort((a,b) => b.tasks.length - a.tasks.length);
    // let rem = [];
    //
    // if (inspectorsArray[0].tasks.length > 1) {
    //   rem = inspectorsArray[0].tasks.splice(-1,1);
    // }
    //
    // this.inspectors.push(new Inspector(id, `Controller #${id}`, getRandomPositionInMoscow(), rem, {}));
  }

  removeInspector() {
    if (this.inspectors.length === 1) {
      this.toastCtrl.create({
        message: 'You can\'t remove all controllers',
        duration: 3000,
        position: 'top'
      }).present();
      return false;
    }

    const removed = this.inspectors.splice(-1,1)[0];

    this.http.post(`https://nwavetest.herokuapp.com/deleteinspector`, {
      id: removed._id
    }).subscribe(data => {
      this.getTasks();
      this.getInspectors();
    });
    // if (this.inspectors.length === 1) {
    //   this.toastCtrl.create({
    //     message: 'You can\'t remove all controllers',
    //     duration: 3000,
    //     position: 'top'
    //   }).present();
    //   return false;
    // }
    //
    // const removed = this.inspectors.splice(-1,1)[0];
    //
    // if (!removed.tasks.length) {
    //   return false;
    // }
    //
    // inspectors.sort((a,b) => a.tasks.length - b.tasks.length);
    //
    // inspectors[0].tasks = inspectors[0].tasks.concat(removed.tasks);
  }

  remove10Minuts() {
    this.http.get(`https://nwavetest.herokuapp.com/time`).subscribe(data => {
      this.getTasks();
      this.getInspectors();
    });
  }

  getTaskById(id) {
    return this.tasks.find(task => task._id === id) || {};
  }

  identify(index, item){
     return item.id_ins;
  }

}



